}
@media screen and (min-width: 768px) {
  .form-checkbox label:not(:first-of-type) {
    margin-top: 0.1875rem;
  }
}

.form-checkbox input {
  display: none;
}

.form-checkbox input + span {
  cursor: pointer;
  display: inline-block;
  padding: 0 0 0 1.375rem;
  position: relative;
  font-size: 1rem;
  line-height: 1.5;
  text-align: left;
}

.form-checkbox input + span::before {
  content: "";
  position: absolute;
  top: 45%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #408F95;
  display: block;
  width: 0.875rem;
  height: 0.875rem;
}
@media screen and (min-width: 768px) {
  .form-checkbox input + span::before {
    top: 49%;
  }
}

.form-checkbox.form-checkbox--consent input + span::before {
  top: 22%;
}
@media screen and (min-width: 768px) {
  .form-checkbox.form-checkbox--consent input + span::before {
    top: 49%;
  }
}

.form-checkbox input + span::after {
  content: "";
  margin-top: -0.125rem;
  position: absolute;
  top: 55%;
  left: 0.125rem;
  transform: translateY(-50%) rotate(-35deg);
  display: block;
  width: 0.625rem;
  height: 0.3125rem;
  border-bottom: 2px solid #C94800;
  border-left: 2px solid #C94800;
  transition: 0.3s;
  opacity: 0;
}

.form-checkbox.form-checkbox-bg input + span::after {
  left: 0.1875rem;
  transform: translateY(-50%) rotate(-35deg);
  display: block;
  width: 1rem;
  height: 0.5rem;
}
@media screen and (min-width: 768px) {
  .form-checkbox.form-checkbox-bg input + span::after {
    top: 45%;
  }
}

.form-checkbox input:checked + span::after {
  opacity: 1;
}

