@use "../global" as *;

.breadcrumb__inner {
  margin: auto;
  padding: 0 rem(15);
  max-width: $inner-sp;
  width: 100%;

  @include mq(md) {
    padding: 0 rem(25);
    max-width: rem(1130);
  }
}

.breadcrumb__inner {
  font-size: rem(14);
  font-weight: $normal;
  color: $black;
}

.breadcrumb__inner.breadcrumb__inner--white {
  color: $white;
}

.breadcrumb__inner .current-item {
  color: $black;
}

.breadcrumb__inner.breadcrumb__inner--white .current-item {
  color: $white;
}
