  @use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
$inner-sp: 600px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 20px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////

$base-font: 'Noto Sans JP', sans-serif;
$english-font: 'Gotu', sans-serif;
$third-font: 'Lato', sans-serif;
// ※検証ツールでしっかり反映されているか確認する

// フォントウェイト
$regular: 300;
$normal: 400;
$medium: 500;
$semibold: 600;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black: #0D2936;
$white: #fff;
$gray: #F2F2F2;
$green: #408F95;
$green2: #DDF0F1;
$red: #C94800;

////////////////////
// 共通指定
////////////////////

body {
  font-family: $base-font;
  color: $black;
}